.gig-container {
    position: relative;
    padding: rem(32px);
    margin: auto;
    max-width: $wide-breakpoint;
    margin-bottom: 0;

    @media(max-width: 600px) {
        padding: 10px;
        flex-direction: column;
        width: 90%;
    }

    .intersection-ref {
        height: 1px;
        width: 1px;
    }

    h1 {
        overflow-wrap: break-word;
        font-size: em(28px);
        line-height: 130%;
        color: $clr-font-dark;
        margin: 0;
        padding-block-end: rem(16px);

        @media(max-width: 600px) {
            font-size: 1.3rem;
            margin-top: 50px;
        }
    }

    h2 {
        line-height: rem(28px);
    }
}

.trash {
    position: absolute;
    inset-inline-start: 1023px;
    inset-block-start: 132px;
}

.gig-details {
    margin-inline-end: 5.5%;
    width: 59.5%;

    @media(max-width: 600px) {
        width: 100%;
    }

    .buyer-msg {
        width: min(100%, 19rem);
        height: 4rem;
        background-color: #ffffff;
        bottom: 32px;
        border-radius: 68px;
        cursor: pointer;
        position: sticky;
        z-index: 100;
        align-items: center;
        padding: 0.5rem 1.5rem 0.5rem 0.5rem;
        box-shadow: 0 0 1px 0.02rem, 0 0.25px 1px 0.03rem, 0 0.5px 2px 0.04rem, 0 2px 4px 0.06rem;

        .owner-img {
            width: 3rem;
            height: 3rem;
            border-radius: 50%;
        }

        .background-dot {
            position: relative;
            bottom: -17px;
            right: 15px;

            .point {
                width: 17px;
                height: 17px;
                background-color: #b5b6ba;
                border-radius: 50%;
                border: 3px #fff solid;

                &.isOnline {
                    background-color: #1dbf73;
                }
            }

        }

        .owner-details {
            font-weight: 700;
            font-size: min(2vh, 1rem);
            line-height: 1rem;

            .response-time {
                margin-top: 2px;
                font-size: 0.75rem;
                font-weight: 300;

                span {
                    font-weight: 700;
                }
            }
        }
    }

    .gig-overview {
        &-filter-nav {
            @media(max-width: 600px) {
                display: none;
            }

            margin-block-end: 10px;

            a {
                font-size: 14px;
                color: $clr-link;
                font-weight: 300;

                &:hover {
                    text-decoration: underline;
                }
            }

            img {
                height: 14px;
                width: 14px;
                margin-inline: 8px;
                margin-block-start: 7px;
            }


        }

        .seller-details {
            margin-bottom: 1rem;
            flex-wrap: wrap;

            >* {
                margin-inline-end: 1em;
                align-items: center;
            }

            & img {
                max-width: 100%;
                width: 30px;
                height: auto;
            }

            .seller-info {
                flex-wrap: wrap;

                >* {
                    padding-inline-end: 8px;
                    align-items: center;
                }
            }
        }
    }

    .gig-imgs-container {
        width: auto;
        position: relative;
        margin-block-end: 64px;
    }

    .review-preview {
        margin-block-end: 64px;

        .review-preview-title {
            color: $clr-font-dark;
        }

        .review-preview-list {
            padding: 20px;
            border-radius: 0.5rem;
            border: 2px solid $clr-border;
        }
    }

    .about-gig {
        &-title {
            padding-bottom: 25px;
        }

        margin-block-end: 96px;
    }

    .seller-details-card-title {
        padding-bottom: 25px;
    }
}

.inbox-msg {
    position: fixed;
    background-color: #fff;
    width: min(80%, 350px);
    bottom: 4rem;
    z-index: 100;
    // transform: translateY(-50%);
    border: $clr-border 1px solid;
    border-radius: .5rem;
    box-shadow: 1px 1px 10px 0px rgba(0, 0, 0, 0.5);

    .details-area {
        padding: 1.25rem;
        flex-wrap: wrap;

        .owner-img {
            width: 3rem;
            height: 3rem;
            border-radius: 50%;
        }

        .background-dot {
            position: relative;
            bottom: -32px;
            right: 16px;

            .point {
                width: 17px;
                height: 17px;
                background-color: #b5b6ba;
                border-radius: 50%;
                border: 3px #fff solid;

                &.isOnline {
                    background-color: #1dbf73;
                }
            }

        }

        .owner-details {
            font-weight: 700;

            .response-time {
                font-size: 0.75rem;
                font-weight: 300;

                span {
                    font-weight: 700;
                }
            }
        }
    }

    .close {
        cursor: pointer;
        position: absolute;
        right: 24px;
        top: 1.5rem;
        font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
        font-weight: 600;
        background-color: rgba(0, 0, 0, 0.1);
        border-radius: 50%;
        width: 1.6rem;
        text-align: center;
    }

    ul {
        margin-top: 2rem;
        padding: 0;
    }

    .msg-line {
        width: 90%;
        margin-inline: auto;
        padding: .5rem;
        text-indent: .5rem;
        border-radius: 1rem;
        list-style: none;
        background-color: rgba(59, 187, 27, 0.1);

        &:nth-child(even) {
            background-color: white;
        }
    }

    form {
        display: flex;
        flex-direction: row;
        padding: 1rem;
    }

    input {
        border-radius: .5rem 0 0 .5rem;
        border: solid 1px rgba(0, 0, 0, 0.5);
        padding: .5rem;
        font-size: 1rem;
        font-family: macan;
        font-weight: 600;
        color: #363636;
        height: 40px;
        width: 100%;

        &:focus-visible {
            outline: none;
        }
    }

    .send-msg {
        color: white;
        background-color: #1dbf73;
        padding: 14px 9px;
        border-radius: 0 .5rem .5rem 0;
        font-size: 1rem;
        justify-content: center;
        margin-inline: auto;
        width: 40px;
        height: 40px;
        margin: 0;
        display: flex;
        align-items: center;
        margin-left: 2px;

        p {
            margin-left: 0.5rem;
        }
    }

}