.packages-container {
    position: relative;
    border: 1px solid $clr-border;
    height: max-content;
    width: 33.6%;
    padding: 0;

    @media(max-width: 600px) {
        width: 100%;
        margin-bottom: 2rem;
    }

    &.sticky {
        position: sticky;
        top: 1rem;
        right: 0;
    }

    h3 {
        margin: 0;
    }

    .labels-container {
        display: flex;
        flex-direction: row;
        align-items: center;

        >* {
            padding: 1rem;
            width: 33%;
            text-align: center;
            flex-grow: 3;
            border-bottom: 1px solid rgb(212, 212, 212);
            border-left: 1px solid rgb(212, 212, 212);
            background-color: rgb(247, 247, 247);
            cursor: pointer;
            caret-color: transparent;

            @media(max-width: 600px) {
                padding-block: 1rem;
                padding-inline: .5rem;
            }

            &:first-child {
                border-left: 0;
            }

        }

        .basic-1,
        .standard-2,
        .premium-3 {
            background-color: white;
            color: $clr-btn-hov;
            border-bottom: 0;
            box-shadow: 0px -2px 0px 0px rgba(27, 173, 54, 1) inset;
        }
    }

    .main-packages {
        padding-top: 2rem;
        padding-inline: 1.5rem;
        padding-bottom: 4px;
        font-size: 0.875rem;
        height: 100%;
        display: flex;
        flex-direction: column;

        h3 {
            text-align: start;
        }

        .packages-header {
            margin-block-end: 63px;

            .packages-details {
                margin-block-end: 24.4px;
            }
        }


        .packages-additional-info {
            margin-block-end: rem(24px);

            >* {
                margin-inline-end: rem(16px);
            }
        }

        button {
            position: relative;
            background-color: black;
            color: white;
            width: 100%;
            border-radius: 4px;
            font-size: 0.875rem;
            padding: .5rem 1rem;
            font-weight: 400;
            height: 2.5rem;
            caret-color: transparent;
            margin-top: 5rem;

            span {
                position: absolute;
                right: 5%;
            }

            &:hover {
                opacity: .7;
                transition: .4s;
            }
        }
    }
}